import { LatLngLiteral, Marker } from '@agm/core';
import { OnInit, ViewChild } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { GetLocationComponent } from '../components/get-location/get-location.component';
import { MarkerType } from '../providers/enums/marker-type.enum';
import { CustomMarker } from '../providers/model/custom-marker.dto';
import { DistanceMatrix, DistanceStatus } from '../providers/model/distance-matrix.model';

declare var google: any;

export class MapUtils implements OnInit {

    // map element
    @ViewChild('map', null) agmMap;
    gmaps: any;
    distanceService: any;

    // map params
    public lat: number = -3.117034;
    public lng: number = -60.025780;
    public zoom: number = 15;

    public costumerPosition: Position;
    public costumerDistance: DistanceMatrix;
    public locationDialog: MatDialogRef<GetLocationComponent>;
    public modalJaExibido = false;
    public naoSuportado = false;

    public cmarkers: CustomMarker[] = [];

    constructor(private dialog: MatDialog) { }

    public ngOnInit(): void {
        this.initMaps();
    }

    async initMaps() {
        const nativeMap = this.agmMap._mapsWrapper.getNativeMap();
        nativeMap.then(map => {
            this.gmaps = map;
            this.distanceService = new google.maps.DistanceMatrixService();
            this.afterInitMaps();
        });
    }

    public buildMarker(position: LatLngLiteral, icon?: string): Marker {
        return new google.maps.Marker({
            position: position,
            icon: icon ? icon : 'assets/mapa/marker-farmacias.png'
        });
    }

    public removeAllFarmaciaMarkers(): void {
        this.cmarkers.forEach(cmarker => {
            if (cmarker.type != MarkerType.CUSTOMER) {
                cmarker.marker.setMap(null);
            }
        });

        this.cmarkers = this.cmarkers.filter(cmarker => {
            return cmarker.type === MarkerType.CUSTOMER;
        });
    }

    public moveToCenter(): void {
        if (this.costumerPosition) {
            const coordinates: LatLngLiteral = { lat: this.costumerPosition.coords.latitude, lng: this.costumerPosition.coords.longitude };
            this.gmaps.panTo(coordinates);
            setTimeout(() => { this.smoothZoom(this.zoom, this.gmaps.getZoom()); }, 500);
            window.scrollTo(0, document.body.scrollHeight);
        } else {
            this.requestPosition();
        }
    }

    public requestPosition = () => {
        if (!this.modalJaExibido) {
            this.locationDialog = this.dialog.open(GetLocationComponent, {
                width: '250px'
            });

            this.locationDialog.afterClosed().subscribe(() => {
                navigator.geolocation.getCurrentPosition(this.geoSuccess, this.geoError);
                window.scrollTo(0, document.body.scrollHeight);
            });
        } else {
            navigator.geolocation.getCurrentPosition(this.geoSuccess, this.geoError);
        }
    };

    public getDistance(origins: LatLngLiteral[], destinations: LatLngLiteral[]): void {
        this.distanceService.getDistanceMatrix(
            {
                origins: origins,
                destinations: destinations,
                travelMode: google.maps.TravelMode.DRIVING,
                unitSystem: google.maps.UnitSystem.METRIC,
                avoidHighways: false,
                avoidTolls: false
            }, (res: DistanceMatrix, status: DistanceStatus) => {
                if (status === DistanceStatus.OK) {
                    this.costumerDistance = res;
                    this.afterLoadDistance();
                } else {
                    console.error(`Erro ao carregar distâncias: ${status}`);
                }
            });
    }

    public geoSuccess = (position) => {
        this.costumerPosition = position;
        this.afterLoadPosition();
    };

    public geoError = (error) => {
        this.requestPosition();
        // switch (error.code) {
        //     case error.PERMISSION_DENIED:
        //         this.requestPosition();
        //         break;
        //     case error.TIMEOUT:
        //         this.requestPosition();
        //         break;
        //     case error.POSITION_UNAVAILABLE:
        //         this.requestPosition();
        //         break;
        //     default:
        //         return;
        // };

    }

    public smoothZoom(max, cnt): void {
        if (cnt >= max) {
            this.zoom = max;
            return;
        }
        else {
            const z = google.maps.event.addListener(this.gmaps, 'zoom_changed', () => {
                google.maps.event.removeListener(z);
                this.smoothZoom(max, cnt + 1);
            });
            setTimeout(() => { this.gmaps.setZoom(cnt); }, 80);
        }
    }

    public afterLoadPosition(): void { }

    public afterLoadDistance(): void { }

    public afterInitMaps(): void { }
}