import { HttpClient, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import { catchError, map } from 'rxjs/operators';

export class RestService {

    private httpOptions = {
        headers: new HttpHeaders({ 'Content-Type': 'application/json' })
    };

    constructor(
        public http: HttpClient
    ) { }

    get(url: string, data?: any): Observable<any> {
        return this.http.get<any>(url).pipe(
            map(this.extractData),
            catchError(this.handleErrorObservable.bind(this))
        );
    }

    post(url: string, data: any): Observable<any> {
        return this.http.post<any>(url, data, this.httpOptions).pipe(
            map(this.extractData),
            catchError(this.handleErrorObservable.bind(this))
        );
    }

    put(url: string, data: any): Observable<any> {
        return this.http.put<any>(url, data, this.httpOptions).pipe(
            map(this.extractData),
            catchError(this.handleErrorObservable.bind(this))
        );
    }
    patch(url: string, data: any): Observable<any> {
        return this.http.patch(url, data).pipe(
            map(this.extractData),
            catchError(this.handleErrorObservable.bind(this))
        );

    }

    delete(url: string, data?: any): Observable<any> {
        const httpOptions = {
            headers: new HttpHeaders({ 'Content-Type': 'application/json' }), body: data
        };

        return this.http.delete<any>(url, httpOptions).pipe(
            map(this.extractData),
            catchError(this.handleErrorObservable.bind(this))
        );
    }

    public handleErrorObservable(error: HttpErrorResponse) {
        let errorText = '';
        if (error.status === 401 || error.status === 403) {
            errorText = 'Acesso negado!';
        } else if (error.status === 0) {
            errorText = 'Falha na conexão!';
        } else {
            errorText = 'Desculpe! Houve um erro interno :(';
        }

        console.error(errorText);
        return Observable.throw(error);

    }

    public extractData(res: any) {
        try {
            const body = res.json();
            return body;
        } catch (error) {
            return res;
        }
    }
}
