import { element } from 'protractor';

export class DistanceMatrix {
    destinationAddresses: string[];
    originAddresses: string[];
    rows: DistanceRow[];
    status: DistanceStatus;

    constructor() {
        this.destinationAddresses = [];
        this.originAddresses = [];
        this.rows = [new DistanceRow()];
    }
}

export enum DistanceStatus {
    OK = 'OK',
    INVALID_REQUEST = 'INVALID_REQUEST',
    MAX_ELEMENTS_EXCEEDED = 'MAX_ELEMENTS_EXCEEDED',
    OVER_DAILY_LIMIT = 'OVER_DAILY_LIMIT',
    OVER_QUERY_LIMIT = 'OVER_QUERY_LIMIT',
    REQUEST_DENIED = 'REQUEST_DENIED',
    UNKNOWN_ERROR = 'UNKNOWN_ERROR'
}

export enum DistanceElementsStatus {
    OK = 'OK',
    NOT_FOUND = 'NOT_FOUND',
    ZERO_RESULTS = 'ZERO_RESULTS',
    MAX_ROUTE_LENGTH_EXCEEDED = 'MAX_ROUTE_LENGTH_EXCEEDED'
}

export class DistanceRow {
    elements: DistanceElement[];

    constructor() {
        this.elements = [];
    }
}

export class DistanceElement {
    distance: DistanceInfo;
    duration: DistanceInfo;
    status: DistanceElementsStatus;
}

export class DistanceInfo {
    text: string;
    value: number;
}