import { LatLngLiteral, Marker } from '@agm/core';
import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MainLoadingService } from 'src/app/components/main-loading/main-loading.service';
import { MarkerType } from 'src/app/providers/enums/marker-type.enum';
import { CustomMarker } from 'src/app/providers/model/custom-marker.dto';
import { Farmacia } from 'src/app/providers/model/farmacia.model';
import { Medicamento } from 'src/app/providers/model/medicamento.model';
import { FarmaciasService } from 'src/app/providers/service/farmacias.service';
import { MapUtils } from 'src/app/utils/map-utils';

@Component({
  selector: 'app-unidades',
  templateUrl: './unidades.component.html',
  styleUrls: ['./unidades.component.scss']
})
export class UnidadesComponent extends MapUtils implements OnInit {
  public farmacias: Farmacia[] = [];
  public nearFarmacias: Farmacia[] = [];
  public farmaciasComMedicamento: Farmacia[] = [];

  public remedio: Medicamento;

  public showPharmList = false;

  constructor(
    private service: FarmaciasService,
    dialog: MatDialog
  ) {
    super(dialog);
  }

  ngOnInit() {
    super.ngOnInit();
  }

  public afterInitMaps(): void {
    this.getFarmacias();
  }

  private getFarmacias(): void {
    this.service.getAll().subscribe(
      success => {
        this.farmacias = success;
        this.buildFarmaciaMarkers(this.farmacias);
        setTimeout(() => { MainLoadingService.hide(); }, 1000);
      },
      () => {
        setTimeout(() => { MainLoadingService.hide(); }, 1000);
      }
    );
  }

  private buildFarmaciaMarkers(farmacias: Farmacia[]): void {
    for (let i = 0; i < farmacias.length; i++) {
      const coordinates: LatLngLiteral = { lat: parseFloat(farmacias[i].latitude), lng: parseFloat(farmacias[i].longitude) };
      const marker: Marker = this.buildMarker(coordinates);

      const cmarker: CustomMarker = new CustomMarker();
      cmarker.type = MarkerType.FARMACIA;
      cmarker.marker = marker;
      cmarker.fieldId = i;
      cmarker.farmacia = farmacias[i];
      cmarker.marker.setMap(this.gmaps);

      this.cmarkers.push(cmarker);
    }
  }

  private createCustomerMarker(coordinates: LatLngLiteral): CustomMarker {
    const marker: Marker = this.buildMarker(coordinates, 'assets/mapa/marker-person.png');

    const cmarker: CustomMarker = new CustomMarker();
    cmarker.type = MarkerType.CUSTOMER;
    cmarker.marker = marker;
    cmarker.marker.setMap(this.gmaps);

    return cmarker;
  }

  public setRemedio(remedio: Medicamento): void {
    this.remedio = new Medicamento();
    Object.assign(this.remedio, remedio);
    this.moveToCenter();
  }

  afterLoadPosition(): void {
    const coordinates: LatLngLiteral = { lat: this.costumerPosition.coords.latitude, lng: this.costumerPosition.coords.longitude };
    this.cmarkers.push(this.createCustomerMarker(coordinates));

    this.gmaps.panTo(coordinates);
    setTimeout(() => { this.smoothZoom(this.zoom, this.gmaps.getZoom()); }, 500);

    this.showOnlyNearFarmacias();
  }

  public gotoPharmacy(cmkr: CustomMarker): void {
    this.togglePharmList();
    const coordinates: LatLngLiteral = { lat: parseFloat(cmkr.farmacia.latitude), lng: parseFloat(cmkr.farmacia.longitude) };
    setTimeout(() => { this.gmaps.panTo(coordinates); }, 500);
  }

  public showOnlyNearFarmacias(): void {
    // TODO: filtrar por farmácias que possuem o Medicamento pesquisado
    this.farmaciasComMedicamento = this.farmacias.slice(60, 80);
    const destinations: LatLngLiteral[] = this.farmaciasComMedicamento.map(farm => { return { lat: parseFloat(farm.latitude), lng: parseFloat(farm.longitude) } });
    const origins: LatLngLiteral[] = [{ lat: this.costumerPosition.coords.latitude, lng: this.costumerPosition.coords.longitude }];
    this.getDistance(origins, destinations);
  }

  afterLoadDistance(): void {
    this.removeAllFarmaciaMarkers();
    this.createCustomMarkersFrom(this.farmaciasComMedicamento);
    this.removeMarkersOutOfRadius(10000);
    console.log(this.cmarkers);
  }

  createCustomMarkersFrom(farmacias: Farmacia[]): void {
    for (let i = 0; i < farmacias.length; i++) {
      const coordinates: LatLngLiteral = { lat: parseFloat(farmacias[i].latitude), lng: parseFloat(farmacias[i].longitude) };
      const marker: Marker = this.buildMarker(coordinates);

      const cmarker: CustomMarker = new CustomMarker();

      cmarker.farmacia = farmacias[i];
      cmarker.distanceInfo = this.costumerDistance.rows[0].elements[i];
      cmarker.type = MarkerType.FARMACIA;
      cmarker.marker = marker;
      cmarker.fieldId = i;
      cmarker.marker.setMap(this.gmaps);

      this.cmarkers.push(cmarker);
    }
  }

  removeMarkersOutOfRadius(raio: number): void {
    const cmarkersAux = [];

    this.cmarkers.forEach((cmkr) => {
      if (cmkr.type === MarkerType.FARMACIA) {
        if (cmkr.distanceInfo.distance.value <= raio) {
          cmarkersAux.push(cmkr);
        } else {
          cmkr.marker.setMap(null);
        }
      }
    });

    cmarkersAux.push(this.cmarkers.find(cmk => cmk.type === MarkerType.CUSTOMER));
    this.cmarkers = [];
    this.cmarkers = cmarkersAux;
  }

  public scrollToTop(): void {
    window.scrollTo(0, 0);
  }

  public togglePharmList(): void {
    this.showPharmList = !this.showPharmList;
  }

}
