import { Injectable } from '@angular/core';
import { RestService } from './rest.service';
import { HttpClient } from '@angular/common/http';
import { EVIRONMENTS } from '../../environments';
import { Observable } from 'rxjs';
import { Medicamento } from '../model/medicamento.model';

@Injectable({
  providedIn: 'root'
})
export class MedicamentosService extends RestService {

  private baseUrl: string = EVIRONMENTS.MANAUS + 'Produtos.php';

  constructor(http: HttpClient) {
    super(http);
  }

  public getAll(): Observable<Medicamento[]> {
    return this.get(this.baseUrl);
  }
  
  public getFilter(query: string): Observable<Medicamento[]> {
    return this.get(`${this.baseUrl}?query=%${query}%`);
  }
}
