import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { CustomMarker } from 'src/app/providers/model/custom-marker.dto';

@Component({
  selector: 'app-farmacy-list',
  templateUrl: './farmacy-list.component.html',
  styleUrls: ['./farmacy-list.component.scss']
})
export class FarmacyListComponent implements OnInit {

  @Input('places') farmacias: CustomMarker[] = [];
  @Output() hideList: EventEmitter<void> = new EventEmitter();
  @Output() showPharmacy: EventEmitter<CustomMarker> = new EventEmitter();

  constructor() { }

  ngOnInit() {
  }

  hidePlaces(): void {
    this.hideList.emit();
  }

  goToPharmacy(cmkr: CustomMarker): void {
    this.showPharmacy.emit(cmkr);
  }

}
