import { Component, ViewChild, OnInit } from '@angular/core';
import { Medicamento } from './providers/model/medicamento.model';
import { UnidadesComponent } from './views/unidades/unidades.component';
import { MainLoadingService } from './components/main-loading/main-loading.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit { 

  @ViewChild('unidades', null) unidadesComponent: UnidadesComponent;

  constructor() {}

  ngOnInit() {
    setTimeout(()=>{MainLoadingService.show();}, 1);
  }

  public showOnMap(remedio: Medicamento): void {
    this.unidadesComponent.setRemedio(remedio);
  }
}
