import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { EVIRONMENTS } from '../../environments';
import { Farmacia } from '../model/farmacia.model';
import { RestService } from './rest.service';

@Injectable({
  providedIn: 'root'
})
export class FarmaciasService extends RestService {

  private baseUrl: string = EVIRONMENTS.MANAUS + 'Unidades.php';

  constructor(http: HttpClient) {
    super(http);
  }

  public getAll(): Observable<Farmacia[]> {
    return this.get(this.baseUrl);
  }

  public getFilter(query: string): Observable<Farmacia[]> {
    return this.get(`${this.baseUrl}?query=%${query}%`);
  }
}
