import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class MainLoadingService {

  public static isProgress = new Subject<boolean>();

  public static show() {
    this.isProgress.next(true);
  }
  
  public static hide() {
    console.log("###################")
    this.isProgress.next(false);
  }
}