import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { FormControl } from '@angular/forms';
import { Medicamento } from 'src/app/providers/model/medicamento.model';
import { MedicamentosService } from 'src/app/providers/service/medicamentos.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {

  @Output() onRemedioSelected = new EventEmitter<Medicamento>();

  private remedios: Medicamento[] = [];
  private medicamento: Medicamento;
  public textQuery = new FormControl();
  public filteredRemedios: Medicamento[] = [];

  constructor(private service: MedicamentosService) {

  }

  ngOnInit() {
    this.getMedicamentos();
    this.textQuery.valueChanges.subscribe(
      (query: string) => {
        this.filteredRemedios = this.remedios.filter((rem: Medicamento) => {
          return rem.descricao.toLowerCase().includes(query.toLowerCase());
        });
      }
    );
  }

  public remedioSelected(): void {
    this.medicamento = this.remedios.find((rem: Medicamento) => rem.descricao.toLowerCase() === this.textQuery.value.toLowerCase());
    this.onRemedioSelected.emit(this.medicamento);
  }

  private getMedicamentos(): void {
    this.service.getAll().subscribe(
      success => {
        this.remedios = success;
        this.filteredRemedios = success;
      }
    );
  }

}
